header {
    height: 100vh;
    padding-top:5rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*============== CTA ==================*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;
    
}

/*============== SOCIALS ==================*/
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left: 3rem;
    padding-top: 3rem;
    font-size: 2rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-white);
}
/*============== ME ==================*/
.me {
    background: linear-gradient(var(--color-white), transparent);
    width: 30rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 50%;
    overflow: hidden;
    transition: var(--transition);
}

.me:hover {
    transform: translateX(-50%) scale(1.1);
}


/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

    .header__socials {
        visibility: hidden;
    }

    .me {
        visibility: hidden;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials {
        visibility: hidden;
    }

    .me {
        visibility: hidden;
    }
}