@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
    height: 100vh;
    padding-top:5rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*============== CTA ==================*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap:1.2rem;
    gap:1.2rem;
    justify-content: center;
    
}

/*============== SOCIALS ==================*/
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap:0.8rem;
    gap:0.8rem;
    position: absolute;
    left: 3rem;
    padding-top: 3rem;
    font-size: 2rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-white);
}
/*============== ME ==================*/
.me {
    background: linear-gradient(var(--color-white), transparent);
    width: 30rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 50%;
    overflow: hidden;
    transition: var(--transition);
}

.me:hover {
    -webkit-transform: translateX(-50%) scale(1.1);
            transform: translateX(-50%) scale(1.1);
}


/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

    .header__socials {
        visibility: hidden;
    }

    .me {
        visibility: hidden;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials {
        visibility: hidden;
    }

    .me {
        visibility: hidden;
    }
}
nav {
    background: transparent;
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.7rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a.active {
    color: var(--color-white);
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-white),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-img {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-img:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap : 1.5rem;
    gap : 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 0.9rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
}

.about__icon {
    color: var(--color-white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-white);
}

.about__content p {
    margin:  2rem 0 2.6rem;
    color: var(--color-white);
    text-align: justify;
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    .about__content {
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }

    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-white);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display : flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-white);
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin : 0 auto;
    }

    .experience__content{
        padding: 1rem;
    }

}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {

    .experience__container {
        grid-gap: 1fr;
        gap: 1fr;
    }

    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }

    
}

.vertical-timeline-element-content {
    box-shadow: 0 0.25rem 0.5rem 0 rgba(0, 0, 0, 0.25), 0 0.4rem 1.25rem 0 rgba(0, 0, 0, 0.15) !important;
    padding: 2rem 3rem !important;
    transition: var(--transition);
}

.vertical-timeline-element-content:hover {
    background: var(--transparent) !important;
    border-color: var(--color-bg-variant);
}

#description {
    margin: 1.5rem 0 2rem 0;
}

@media screen and (max-width: 1024px) {
    .vertical-timeline-element-date {
        display: block !important;
        float: none !important;
        color: rgb(44,44,44);
        margin-top: 1.5rem;
    }

}


.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    background: transparent;
    border-color: var(--transparent);
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item h5 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap:1.2rem;
    gap:1.2rem;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
    width: 100%;
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom:0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form {
    display: flex;
    flex-direction:column;
    grid-gap:1.2rem;
    gap:1.2rem;
}

input, textarea {
    color: var(--color-white);
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
}

/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
    }
}
footer {
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-primary);
}

.permalink {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 20rem;
    gap: 20rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    font-size: 2rem;
    grid-gap:0.8rem;
    gap:0.8rem;
    margin-bottom: 4rem;
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-primary);
}

@media screen and (max-width: 1024px) {
    .permalink {
        grid-gap: 4rem;
        gap: 4rem;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    .permalink {
        flex-direction: column;
        grid-gap: 4rem;
        gap: 4rem;
    }
}
* {
     margin:  0;
     padding: 0;
     border: 0;
     outline: 0;
     box-sizing: border-box;
     list-style: none;
     text-decoration: none;
 }

 :root {
     --color-bg-variant: #B5B1B2 /*ADA9B7*//*B3B6B7*/;
     --color-primary-variant: #9DD9D2;
     --color-white: white;
     --color-black: black;

     --transition: all 400ms ease;

     --container-width-lg: 75%;
     --container-width-md: 86%;
     --container-width-ms: 90%;

 }

 html {
     scroll-behavior: smooth;
 }

 ::webkit-scrollbar {
     display: none;
 }

 body {
    font-family: 'Poppins', sans-serif;
    background: black;
    background: var(--color-black);
    color: white;
    color: var(--color-white);
    line-height:  1.8;
    cursor: default;
 }

 .container {
     width: 75%;
     width: var(--container-width-lg);
     margin: 0 auto;
 }

 h1, h2, h3, h4, h5 {
     font-weight: 500;
     font-size: 1rem;
 }

 h1 {
     font-size: 3rem;
     font-family: "Poppins SemiBold";
 }

 h5 {
     font-family: "Poppins Light";
 }

 section {
     margin-top: 9rem;
 }

 section > h2, section > h5 {
    color: white;
    color: var(--color-white);
    margin-bottom: 3rem;
    text-align: center;
 }


.text-light {
    color: white;
    color: var(--color-white);
}

 a {
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
 }

 a:hover {
     color: black;
     color: var(--color-black);
 }

 .btn {
     width: -webkit-max-content;
     width: max-content;
     display: inline-block;
     color: white;
     color: var(--color-white);
     padding: 0.75rem 1.2rem;
     border-radius:  0.4rem;
     cursor: pointer;
     border: 1px solid var(--color-primary);
     transition: all 400ms ease;
     transition: var(--transition);
 }

 .btn:hover {
     background: white;
     background: var(--color-white);
     color: black;
     color: var(--color-black);
     border-color: transparent;
 }

 .btn-primary {
    background: white;
    background: var(--color-white);
    color: black;
    color: var(--color-black);
}

img {
    max-width: 100%;  
    height: auto; 
}


/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */

@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */

@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-ms);
    }

    section {
        margin-bottom: 6rem;
    }
}
